"use client";

import { useEffect, useState } from "react";
import { createClient } from "@/utils/supabase/client";
import { Recording } from "@/types/recording";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

import { Input } from "@/components/ui/input";
import {
  Mic,
  FileText,
  Clock,
  Timer,
  Search,
  FolderOpen,
  Tag,
  Settings2,
} from "lucide-react";

export default function DashboardPage() {
  const [recordings, setRecordings] = useState<Recording[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchRecordings() {
      try {
        const supabase = createClient();
        const { data, error } = await supabase
          .from("recordings")
          .select("*")
          .order("created_at", { ascending: false });

        if (error) throw error;
        setRecordings(data || []);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : "Failed to fetch recordings"
        );
      } finally {
        setLoading(false);
      }
    }

    fetchRecordings();
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-red-500">{error}</div>
      </div>
    );
  }

  return (
    <div className="container mx-auto p-6 space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-semibold">Mine opptak</h1>
        <div className="flex space-x-2">
          <Button variant="ghost" size="sm">
            I dag
          </Button>
          <Button variant="ghost" size="sm">
            Denne uken
          </Button>
          <Button variant="ghost" size="sm">
            Denne måneden
          </Button>
          <Button variant="secondary" size="sm">
            Totalt
          </Button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              Totalt antall opptak
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">0</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Timer totalt</CardTitle>
            <Clock className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">0t 0m</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Tid spart</CardTitle>
            <Timer className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">0m</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              Snitt varighet
            </CardTitle>
            <Clock className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">0s</div>
          </CardContent>
        </Card>
      </div>

      <div className="flex space-x-4">
        <Button className="flex items-center space-x-2" size="lg">
          <Mic className="h-5 w-5" />
          <span>Start opptak</span>
        </Button>
        <Button
          variant="outline"
          className="flex items-center space-x-2"
          size="lg"
        >
          <FileText className="h-5 w-5" />
          <span>Last opp opptak</span>
        </Button>
      </div>

      <div className="flex justify-between items-center space-x-4">
        <div className="flex-1">
          <div className="flex items-center space-x-4">
            <div className="flex-1">
              <div className="flex items-center border rounded-lg px-3 py-2">
                <Search className="h-5 w-5 text-gray-400" />
                <Input
                  className="border-0 focus:ring-0"
                  placeholder="Søk i opptak, transkripsjoner, etiketter..."
                />
              </div>
              <p className="text-sm text-gray-500 mt-1">
                Søk i titler, etiketter og mapper
              </p>
            </div>
            <Button variant="outline">
              <Settings2 className="h-5 w-5" />
              <span>Rediger meter</span>
            </Button>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-5 gap-4">
        <div className="col-span-1 space-y-4">
          <div className="space-y-2">
            <h3 className="flex items-center space-x-2 font-medium">
              <FolderOpen className="h-5 w-5" />
              <span>Mapper</span>
            </h3>
            <Button variant="ghost" className="w-full justify-start">
              Alle mapper
            </Button>
          </div>
          <div className="space-y-2">
            <h3 className="flex items-center space-x-2 font-medium">
              <Tag className="h-5 w-5" />
              <span>Etiketter</span>
            </h3>
          </div>
        </div>
        <div className="col-span-4">
          <div className="text-center text-gray-500 py-8">
            Ingen opptak funnet
          </div>
        </div>
      </div>
    </div>
  );
}
